import { Component, Input, OnChanges, Output, SimpleChanges, EventEmitter } from '@angular/core';

@Component({
    selector: 'n52-string-toggler',
    template: `<button type="button" class="btn" (click)="toggle()" [ngClass]="isToggled ? 'btn-primary' : 'btn-light'" title="{{tooltip}}">
    <i class="fa fa-{{icon}}" aria-hidden="true"></i>
</button>`
})
export class StringTogglerComponent implements OnChanges {

    @Input()
    public value: string;

    @Input()
    public option: string;

    @Input()
    public icon: string;

    @Input()
    public tooltip: string;

    @Output()
    public onToggled: EventEmitter<string> = new EventEmitter();

    public isToggled: boolean;

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.value) {
            this.isToggled = this.option === this.value;
        }
    }

    public toggle() {
        this.onToggled.emit(this.option);
    }
}
